<template>
	<div>
		<div v-if="chapter.id" uk-sticky="sel-target: .uk-navbar-container; cls-active: uk-navbar-sticky">
			<nav id="it-nav-viewer" class="uk-navbar-container uk-light uk-visible@m" uk-navbar>
				<div class="uk-navbar-left">
					<router-link :to="back" class="uk-navbar-item uk-logo">
						{{'BACK' | translate}}
					</router-link>
				</div>
				<div class="uk-navbar-center">
					<ul class="uk-navbar-nav" v-if="chapter">
						<li><a href="#" @click="viewFit('fit-height')">{{'FIT_HEIGHT' | translate}} ↕</a></li>
						<li><a href="#" @click="viewFit('fit-width')">{{'FIT_WIDTH' | translate}} ↔</a></li>

						<li class="uk-visible@l" v-if="!isFullscreen">
							<a href="#" @click="go_full_screen">{{'FULLSCREEN' | translate}} <span uk-icon="expand" class="uk-margin-small-left"></span></a>
						</li>
						<li class="uk-visible@l" v-if="isFullscreen">
							<a href="#" @click="close_full_screen">{{'FULLSCREEN_EXIT' | translate}} <span uk-icon="expand" class="uk-margin-small-left"></span></a>
						</li>
					</ul>
				</div>

				<div class="uk-navbar-right">
					<ul class="uk-navbar-nav" v-if="chapter">
						<li v-if="!previous"><a href="#" class="uk-disabled"><span uk-icon="chevron-left" class="uk-margin-small-right"></span> {{'PREVIOUS' | translate}}</a></li>
						<li v-else><router-link :to="previous"><span uk-icon="chevron-left" class="uk-margin-small-right"></span> {{'PREVIOUS' | translate}}</router-link></li>

						<li v-if="!next"><a href="#" class="uk-disabled">{{'NEXT' | translate}} <span uk-icon="chevron-right" class="uk-margin-small-left"></span></a></li>
						<li v-else><router-link :to="next">{{'NEXT' | translate}} <span uk-icon="chevron-right" class="uk-margin-small-left"></span></router-link></li>
					</ul>
				</div>
			</nav>
		</div>
		<nav id="it-nav-viewer">
			<a class="uk-position-left uk-overlay uk-flex uk-flex-middle uk-disabled" v-if="!previous"></a>
			<router-link :to="previous" class="uk-position-left uk-overlay uk-flex uk-flex-middle" v-else>
				{{'PREVIOUS' | translate}}
			</router-link>

			<a class="uk-position-right uk-overlay uk-flex uk-flex-middle uk-disabled" v-if="!next"></a>
			<router-link :to="next" class="uk-position-right uk-overlay uk-flex uk-flex-middle" v-else>
				{{'NEXT' | translate}}
			</router-link>
		</nav>
	</div>
</template>

<script>
	import { mapMutations } from 'vuex';
	export default {
		name: "NavPages",
		props: {
			chapter_page: Object,
			chapter: Object
		},
		data() {
			return {
				isActive: false,
				isFullscreen: false,
			}
		},
		computed:{
			back() {
				/*if (this.chapter.series.has_chapter) {
					return '/serie/'+ this.chapter.series.id +'/chapter/' + this.chapter.id
				}
				else {
					return '/serie/'+ this.chapter.series.id
				}*/
				return '/serie/'+ this.chapter.series.id
			},
			next(){
				let index = this.currentIndex();
				if (this.maxPages() > index + 1 ) {
					return this.urlFor(index + 1);
				}
				return null;
			},
			previous(){
				let index = this.currentIndex();
				if (index > 0) {
					return this.urlFor(index - 1);
				}
				return null;
			}
		},
		methods:{
			...mapMutations('viewer',[
				'setFitClass'
				]),
			maxPages(){
				return this.chapter.chapter_pages.length;
			},
			currentIndex(){
				return this.chapter.chapter_pages.indexOf(this.chapter_page);
			},
			urlFor(index) {
				return { path: '/viewer/' + this.chapter.id + '/' + this.chapter.chapter_pages[index]?.id };
			},
			viewFit (value) {
				this.setFitClass(value);
			},
			go_full_screen(){
				this.isFullscreen = true;
				var elem = document.documentElement;
				if (elem.requestFullscreen) {
					elem.requestFullscreen();
				} else if (elem.msRequestFullscreen) {
					elem.msRequestFullscreen();
				} else if (elem.mozRequestFullScreen) {
					elem.mozRequestFullScreen();
				} else if (elem.webkitRequestFullscreen) {
					elem.webkitRequestFullscreen();
				}
			},
			close_full_screen(){
				this.isFullscreen = false;
				if (document.exitFullscreen) {
					document.exitFullscreen();
				} else if (document.mozCancelFullScreen) {
					document.mozCancelFullScreen();
				} else if (document.webkitCancelFullScreen) {
					document.webkitCancelFullScreen();
				}
			}
		}
	};
</script>
